.flex {
  display: flex;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.flex-one {
  display: flex;
  flex: 1;
}
.flex-zero {
  display: flex;
  flex: 0;
}
.full-height {
  height: 100%;
}
.full-width {
  width: 100%;
}
ul {
  list-style: none;
}
.page-header {
  font-size: 36px;
}
.list {
  margin: 25px;
}
.card {
  background-color: white;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 2px 5px 0px #999;
  padding: 15px;
  border-top: 2px solid #0d9263;
}
.card.no-border {
  border-top: none;
}
.tabs {
  display: flex;
  flex-direction: row;
  margin-top: 15px;
  margin-left: 5px;
  margin-bottom: -15px;
  list-style: none;
}
.tabs li {
  margin-right: 15px;
  padding: 0 10px;
}
.tabs li.active {
  border-bottom: 3px solid #0d9263;
}
.tabs li.active a {
  color: #0d9263;
}
.tabs li a {
  color: #494b4b;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
}



